@media screen and (max-width: 767px) {
  body.lock {
    overflow: hidden;
  }

  .header {
    width: 100%;
    z-index: 55;
    position: fixed;
    top: 0;
    left: 0;
  }

  .header__container {
    padding: 20px 15px;
