  .header__burger {
    width: 30px;
    height: 20px;
    z-index: 3;
    display: block;
    position: relative;
  }

  .header__burger span {
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all .3s;
    position: absolute;
    top: 9px;
    left: 0;
  }

  .header__burger:before, .header__burger:after {
    content: "";
    width: 100%;
    height: 2px;
    transition: var(--transition);
    background-color: #fff;
    position: absolute;
    left: 0;
  }

  .header__burger:before {
    top: 0;
  }

  .header__burger:after {
    bottom: 0;
  }

  .header__burger.active span {
    transform: scale(0);
  }

  .header__burger.active:before {
    top: 9px;
    transform: rotate(45deg);
  }

  .header__burger.active:after {
    bottom: 9px;
    transform: rotate(-45deg);
  }

  .header__menu {
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: rgba(89, 89, 89, .91);
    padding: 90px 10px 20px;
    position: fixed;
    top: -100%;
    left: 0;
    overflow: auto;
  }

  .header__menu.active {
    transition: var(--transition);
    top: 0;
  }

  .menu__list {
    display: block;
  }

  .menu__item {
    margin-bottom: 20px;
  }

  .menu__link:hover, .menu__link:focus {
    color: #000;
